.wp-block-simplecal-calendar {
	color: #323232;
	padding: 4px;
}

.simplecal_event_meta {
	display: table;
	.simplecal_event_meta_row {
		display: table-row;
	}
	.simplecal_event_meta_label {
		display: table-cell;
		padding-right: 0.5em;
		text-transform: uppercase;
		&::after {
			content: ':';
		}
	}
	.simplecal_event_meta_value {
		display: table-cell;
	}

}

.simplecal_list_item_excerpt {
	font-size: 1rem;
}

.simplecal_events_wrapper.simplecal_agenda_layout1 {
	overflow-y: scroll;
	scrollbar-color: #a0a0a0 rgba(0,0,0,0);
	scrollbar-gutter: stable;
}

.simplecal_no_events {
	font-style: italic;
	text-align: center;
	margin: 1em auto;
}

