.simplecal_theme1 {
	.simplecal_nav_pagination {
		display: flex;
		flex-flow: row nowrap;
		justify-content: space-between;
	}
	.simplecal_nav_prev, .simplecal_nav_next {
		opacity: 0.5;
		display: flex;
		flex-flow: row nowrap;
		column-gap: 0.25em;
		justify-content: center;
		transition: 0.25s all ease-in-out;
		align-items: center;
		cursor: default;
		&.active {
			opacity: 1;
			cursor: pointer;

			&:hover {
				font-weight: bold;
			}
		}

		span { line-height: unset !important;}
	}
	
	.simplecal_list_year_header, .simplecal_list_month_header {
		margin: 0.25em 0;
		font-size: 1.5em;
		font-weight: bold;
	}
	.simplecal_list_month_header {
		font-size: 1.25em;
	}

	.simplecal_list_month_header ~ .simplecal_list_item_layout1 {
		margin-left: 2em;
	}
}

.simplecal_list_item_layout1 + .simplecal_list_item_layout1 {
	margin-top: 2em;
}

.simplecal_list_item_layout1 {
	display: flex;
	flex-flow: row nowrap;

	.simplecal_list_item_thumbnail {
		flex: 0 0 100px;
		margin-right: 10px;
		img {
			max-width: 100%;
		}
	}
	.simplecal_list_item_content_wrapper {
		flex: 1 1 auto;
		display: flex;
		flex-flow: column nowrap;;
		a {
			text-decoration: none;
			color: #1b4a70;
		}
		.simplecal_list_item_meta {
			display: flex;
			flex-flow: row nowrap;
			column-gap: 0.5em;
		}

		.simplecal_list_item_title {
			font-size: 1.25em;
			font-weight: bold;
		}
		.simplecal_list_item_datetime {
			color: #606060;
		}
		.simplecal_list_item_datetime {
			display: none;
		}

		.simplecal_list_item_excerpt {
			p:first-of-type {
				margin-top: 0;
			}
		}

		.material-symbols-outlined {
			vertical-align: middle;
			font-size: 1em;
		}
	}
}

.simplecal_past_event {
	opacity: 0.6;
}

.simplecal_events_wrapper.simplecal_agenda_layout2 {
	display: flex;
	flex-flow: row wrap;
	gap: 0.5em;

	.simplecal_list_item {
		padding: 0.5em;
		border: 1px solid #baba;
		border-radius: 4px;
		flex: 1 0 400px;

		@media only screen and (min-width: 768px) {
			flex: 0 0 calc(50% - 1.5em);
			max-width: calc(50% - 1.5em);
		}
		@media only screen and (min-width: 1200px) {
			flex: 0 0 calc(33% - 1.5em);
			max-width: calc(33% - 1.5em);
		}
	}

	.simplecal_list_item_container + .simplecal_list_item_container {
		margin-top: 0.5em;
	}

	.simplecal_list_item_meta {
		display: flex;
		flex-flow: row nowrap;
		gap: 0.25em;
		align-items: center;

		.simplecal_list_item_meta_data {
			overflow: hidden;
			text-overflow: ellipsis;
			text-wrap: nowrap;
		}

		span { line-height: unset !important;}
	}

	.simplecal_list_item_datetime {
		display: flex;
		flex-flow: column nowrap;
		justify-content: flex-start;
	}
	.simplecal_list_item_date {
		display: flex;
		flex-flow: row wrap;
		justify-content: space-between;

		.simplecal_list_item_dates {
			font-weight: bold;
		}
		.simplecal_list_item_dayofweek {
			font-size: 0.75em;
		}
	}

	.simplecal_list_item_time, .simplecal_list_item_location, .simplecal_list_item_website {
		font-size: 0.8em;
	}

	.simplecal_list_item_title {
		font-size: 1.25em;
		
		@media only screen and (min-width: 768px) {
			overflow: hidden;
			text-wrap: nowrap;
			text-overflow: ellipsis;
		}

		a {
			text-decoration: none;

			&:hover {
				text-decoration: underline;
			}
		}
	}
}